var canvas = document.getElementById("myCanvas");
var ctx = canvas.getContext("2d");


var score = 0;


function drawScore() {
    ctx.font = "16px Arial";
    ctx.fillStyle = "#0095DD";
    ctx.fillText("Score: " + score, 8, 20);
}




function draw() {

    drawScore();

}


